html, body {
  height: 100%;
  margin: 0;
}
.App-logo {
  height: 50px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content {
  overflow: hidden;
  height: calc(100vh - 76px);
  padding-left: 15px;
  padding-right: 15px;
}

.tableFixHead { 
  overflow-y: auto; 
}

.tableFixHead thead th { 
  position: sticky; 
  top: 0; 
  border-top: 1px;
}

.tableFixHead::-webkit-scrollbar-track
{
	box-shadow: inset 0 0 6px rgba(0,0,0,0.1);
	background-color: #F5F5F5;
	border-radius: 10px;
}

.tableFixHead::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

.tableFixHead::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	background-color: #FFF;
	background-image: -webkit-gradient(linear, 40% 0%, 75% 84%, from(#94c8ff), to(#94c8ff), color-stop(.6,#93c8ff));
}

.cursor-pointer {
  cursor: pointer;
}

.number {
  font-size: 10rem;
}